import { makeAutoObservable, configure } from "mobx";
// @ts-ignore
import { v4 as uuidv4 } from "uuid";

// eslint-global Gun

configure({
  enforceActions: "never",
  useProxies: "ifavailable",
});

// GUN
var currentStore: Store;
var broken = true;
var gun: any;

function onConnect() {
  if (broken) {
    console.log("(re)creating Gun connection");
    broken = false;
    // @ts-ignore
    gun = Gun({
      peers: ["https://ts.nas-system.cz/gun"],
      onConnect: onConnect,
      onDisconnect: onDisconnect,
      uuid: uuidv4,
    });

    // @ts-ignore
    gun.on("auth", onAuth);
    // @ts-ignore
    gun.user().recall({ sessionStorage: true });
    // @ts-ignore
    window.gun = gun;
  }
}

function onDisconnect() {
  broken = true;
}

function onAuth(params: any) {
  currentStore.mapPropertiesToGun();
}

const ANONYMOUS = { pub: null, alias: "Host" };

class Store {
  user = ANONYMOUS;
  error = "";
  todos = {};

  constructor() {
    makeAutoObservable(this);
    // @ts-ignore
    window.store = this;
    currentStore = this;
    onConnect();
  }

  get userAlias(): string {
    return this.user.alias;
  }

  mapPropertiesToGun() {
    console.log("map props to gun");
    // @ts-ignore
    if (gun.user().is) {
      const self = this;
      gun.user(gun.user().is.pub).on(function (x: any) {
        // ...or once?
        // @ts-ignore
        //self.user = { pub: self.user.pub, alias: x.alias };
        self.user.alias = x.alias;
      });
    }
    // @ts-ignore
    gun
      .user()
      .get("todos")
      .map()
      .on((say: any, id: string) => {
        if (say) {
          // @ts-ignore
          this.todos = { ...this.todos, [id]: say };
        } else {
          // @ts-ignore
          this.todos = _.omit(this.todos, [id]);
        }
      });
  }

  cleanData() {
    gun.user(this.user.pub).off();
    gun.user().get("todos").map().off();
    this.todos = {};
  }

  isLoggedIn() {
    // @ts-ignore
    if (!gun.user().is) {
      this.setError("Nep≈ôihl√°≈°en√Ω u≈æivatel");
      return false;
    } else {
      this.error = "";
      return true;
    }
  }

  addTodo(s: string) {
    if (!this.isLoggedIn()) {
      return;
    }
    const uid = uuidv4();
    // @ts-ignore
    gun
      .user()
      .get("todos")
      .get(uid)
      .put({ uid: uid, text: s, created: new Date().toISOString() });
  }

  changeTodo(id: string, val: string) {
    gun
      .user()
      .get("todos")
      .get(id)
      .once(function (x: any) {
        gun.user().get("todos").get(id).put({
          uid: id,
          text: val,
          created: x.created,
          updated: new Date().toISOString(),
        });
      });
  }

  deleteTodo(id: string) {
    if (!this.isLoggedIn()) {
      return;
    }
    // @ts-ignore
    gun.user().get("todos").get(id).put(null);
  }

  setError(s: string) {
    this.error = s;
    // @ts-ignore
    var self = this;
    setTimeout(function () {
      self.error = "";
    }, 3000);
  }

  createUser(login: string, password: string) {
    if (!login) {
      this.setError("Nezadali jste p≈ôihla≈°ovac√≠ jm√©no");
      return;
    }
    if (!password) {
      this.setError("Nezadali jste heslo");
      return;
    }
    if (password.length < 5) {
      this.setError("Heslo mus√≠ b√Ωt aspo≈à 5 znak≈Ø dlouh√©");
      return;
    }
    // @ts-ignore
    gun.user().create(login, password, (x) => {
      console.log("xxx", x);
      if (x.err) {
        this.setError("U≈æivatel ji≈æ existuje");
      }
    });
  }

  login(login: string, password: string) {
    // @ts-ignore
    gun.user().auth(login, password, (x) => {
      if (x.err) {
        this.setError("Neplatn√© p≈ôihl√°≈°en√≠");
      }
    });
  }

  logout() {
    // @ts-ignore
    this.cleanData();
    this.user = ANONYMOUS;
    gun.user().leave();
  }
}

export { Store };
