import React, { useState } from "react";
import { observer } from "mobx-react-lite";
import { entries } from "mobx";
import { Store } from "./store";
// @ts-ignore
import _ from "lodash";

const App = observer((props: { store: Store }) => {
  const [login, setLogin] = useState("");
  const [pwd, setPwd] = useState("");
  const [todo, setTodo] = useState("");

  return (
    <>
      <div style={{ float: "right" }}>üë§ {props.store.userAlias}</div>
      <br></br>
      {props.store.error && (
        <div
          style={{
            backgroundColor: "#ffdddd",
            color: "red",
            marginLeft: "auto",
            marginRight: "auto",
            width: "50%",
            border: "solid 1px red",
            padding: "1em",
          }}
        >
          {props.store.error}
        </div>
      )}
      <br></br>
      <pre>
        {_.chain(entries(props.store.todos))
          // @ts-ignore
          .orderBy((x) => x[1]["created"])
          // @ts-ignore
          .map((x) => {
            return (
              <div
                key={x[0]}
                style={{
                  width: "50%",
                  backgroundColor: "seashell",
                  margin: "auto",
                  marginBottom: "0.5em",
                  padding: "1em",
                }}
                onClick={() => {
                  let val = prompt("Upravit:", x[1]["text"]);
                  if (val) {
                    props.store.changeTodo(x[1]["uid"], val);
                  }
                }}
              >
                {x[1]["text"]}
                &nbsp;
                <span
                  title="Smazat z√°znam"
                  style={{
                    float: "right",
                    cursor: "pointer",
                  }}
                  onClick={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    props.store.deleteTodo(x[0]);
                  }}
                >
                  &times;
                </span>
              </div>
            );
          })
          .value()}
      </pre>
      +{" "}
      <input
        value={todo}
        placeholder="Zadej text a stiskni Enter"
        autoFocus
        onKeyPress={(e) => {
          if (e.key === "Enter") {
            props.store.addTodo(todo);
            setTodo("");
          }
        }}
        onChange={(e) => {
          setTodo(e.target.value);
        }}
      ></input>
      <hr></hr>
      üë§{" "}
      <input
        value={login}
        onChange={(e) => {
          setLogin(e.target.value);
        }}
      />
      <br></br>
      üîë{" "}
      <input
        value={pwd}
        type="password"
        onChange={(e) => {
          setPwd(e.target.value);
        }}
        onKeyPress={(e) => {
          if (e.key === "Enter") {
            // @ts-ignore
            props.store.login(login, pwd);
          }
        }}
      />
      <br></br>
      <button
        onClick={() => {
          // @ts-ignore
          props.store.login(login, pwd);
        }}
      >
        P≈ôihl√°sit
      </button>
      <button
        onClick={() => {
          // @ts-ignore
          props.store.createUser(login, pwd);
        }}
      >
        Registrovat
      </button>
      <br></br>
      <button
        onClick={() => {
          // @ts-ignore
          props.store.logout();
        }}
      >
        Odhl√°sit
      </button>
      <button
        onClick={() => {
          localStorage.clear();
          sessionStorage.clear();
          window.location.reload();
        }}
      >
        Resetovat data
      </button>
    </>
  );
});

export default App;
